.Nav.Link {
  color: #000000;
  text-decoration: underline;
}

.carousel-caption {
  position: relative;
  left: 0;
  bottom: 0;
  opacity: 0.3;
  background-color: #052232;
  border-radius: 20px;
}
.carousel-caption:hover {
  position: relative;
  left: 0;
  bottom: 0;
  opacity: 0.8;
  background-color: #052232;
  border-radius: 0px;
}

.light-cardtext {
  color: #000000;
}

.h1 {
  color: #000000;
}

.App-link {
  color: #61dafb;
}

img {
  width: 100%;
  height: auto;
}

.button-wrapper {
  margin-top: 20px;
}
.footer-card {
  max-height: 50vh;
  background-color: #ffffff;
  padding: 20px;
  border-radius: 10px;
}
.profile-pic {
  border: 10px solid white;
  border-radius: 50%;
  max-width: 50vw;
  max-height: 50vh;
  width: auto;
  height: auto;
  z-index: 10;
}
.image-container {
  z-index: 10;
  margin-top: 30px;
}

.call-to-action {
  border-radius: 50%;
  padding: 20%;
}
.call-to-action {
  border-radius: 50%;
}
.titles {
  background-color: #ffffff;
  color: #000000;
  margin-top: -25vh;
  padding-top: 25vh;
  margin-bottom: 30px;
  width: 75vw;
  border-radius: 10px;
  z-index: 1;
}

.resumedownload {
  
  
  color: #000000;
  min-height: 100vh; 
  margin-bottom: 30px;
  margin-top: 10vh;
  
  width: 50vw;
  z-index: 1;
}
@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
